import cuCustom from '@/colorui/components/cu-custom.vue'
export default {
	onLoad(e) {
		this.shopid = e.shopid
		console.log(e)
		uni.request({
			url: `http://47.104.29.236:9999/api/v1/shopId/` + this.shopid,
			method: 'GET',
			data: {},
			success: res => {
				this.shopData = res.data.data[0]
				this.imgList = res.data.imgList
				console.log(res)
				// uni.hideLoading()
			},
			fail: () => {},
			complete: () => {}
		});
		console.log(e)
	},
	data() {
		return {
			// 控制自定义导航栏是否字体加粗
			isBold: true,
			// 发起人id
			userId: '',
			shopData: {},
			user: {},
			imgList: []
		}
	},
	methods: {
		// 点击购买
		dianji() {
			uni.setStorage({
				key: 'storage_key',
				data: {
					shopid: this.shopid
				},
				success: function() {}
			});
			uni.navigateTo({
				url: '../purchase/purchase',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	components: {
		cuCustom,
	}
}
